body {
  font-family: "FiraSans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 170%;
  color: #484848; }

.raleway-font {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 21px;
  line-height: 25px;
  color: #484848; }

.title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-size: 40px;
  line-height: 49px;
  font-weight: 700;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #1FC0FC; }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(235, 235, 235, 0.95);
  z-index: 10; }

.modal__inner {
  width: 500px;
  height: 520px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 56px 72px 40px 48px;
  border-radius: 8px;
  box-shadow: 0px 13px 23px 0px rgba(72, 72, 72, 0.7);
  background-color: #ffffff; }

.modal__inner h3 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-size: 28px;
  line-height: 24px;
  font-weight: 700;
  letter-spacing: -0.02em;
  color: #1FC0FC; }

.modal__inner p {
  font-size: 15px;
  line-height: 170%;
  opacity: 0.87;
  max-width: 360px; }

.modal__inner input[type="text"] {
  width: 360px;
  height: 59px;
  box-sizing: border-box;
  padding: 20px 40px;
  margin-bottom: 8px;
  border: 2px solid #E3E3E3;
  border-radius: 80px; }
  .modal__inner input[type="text"]::placeholder {
    font-size: 17px;
    line-height: 20px;
    font-weight: 500;
    font-variant: small-caps;
    text-transform: uppercase;
    opacity: 0.5; }

.modal__inner button[type="submit"],
.modal__inner .modal__submit {
  font-size: 17px;
  line-height: 20px;
  font-weight: 500;
  font-variant: small-caps;
  color: #ffffff;
  text-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12);
  display: block;
  width: 265px;
  height: 59px;
  margin: 0 auto;
  margin-top: 25px;
  margin-bottom: 16px;
  border: none;
  border-radius: 80px;
  background: linear-gradient(108.42deg, #FFC341 20.46%, #FFD701 65.31%); }

.modal__inner input[type="checkbox"] {
  width: 16px;
  height: 16px;
  border: 2px solid #FFC341;
  border-image: linear-gradient(108.42deg, #FFC341 20.46%, #FFD701 65.31%);
  border-image-slice: 1;
  border-radius: 2px;
  background-color: #ffffff;
  -webkit-appearance: none;
  appearance: none; }

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 18px;
  height: 18px;
  border: none;
  background-color: transparent; }
  .modal__close::before, .modal__close::after {
    content: "";
    width: 25px;
    height: 2px;
    position: absolute;
    top: 8px;
    left: -4px;
    background: linear-gradient(108.42deg, #FFC341 20.46%, #FFD701 65.31%); }
  .modal__close::before {
    transform: rotate(45deg); }
  .modal__close::after {
    transform: rotate(-45deg); }

/* blocks */
.header {
  position: relative;
  margin: 0 auto;
  background-image: url(../img/header-bg.png);
  background-repeat: no-repeat;
  background-size: cover; }

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 48px 110px;
  margin-bottom: 400px; }

.header__tel {
  font-size: 15px;
  line-height: 18px;
  color: #484848;
  text-decoration: none;
  order: -1; }

.header__feedback {
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0;
  border: none;
  border-bottom: 2px dashed #484848;
  background-color: transparent; }

.header__text {
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  max-width: 350px;
  margin: 0 auto;
  margin-bottom: 40px; }

.header__scroll-icon {
  display: block;
  margin: 0 auto;
  position: relative;
  z-index: 2; }

@media (min-resolution: 144dpi) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/header-bg@2x.png"); } }

@media (max-width: 767px) {
  .header {
    min-height: 600px;
    background-image: url("../img/header-bg-mobile.png"); }
  .header__wrapper {
    position: relative;
    display: block;
    margin-bottom: 225px;
    padding: 20px 7px 16px 20px; }
  .header__logo img {
    width: 120px;
    height: 42px; }
  .header__tel {
    font-size: 14px;
    line-height: 17px;
    font-weight: 500;
    order: unset;
    position: absolute;
    top: 20px;
    right: 7px; }
  .header__feedback {
    font-size: 14px;
    line-height: 17px;
    font-weight: 500;
    position: absolute;
    top: 45px;
    right: 7px; }
  .header__text {
    font-size: 20px;
    line-height: 24px; } }

@media (max-width: 767px) and (min-resolution: 144dpi) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/header-bg-mobile@2x.png"); } }

.about {
  min-height: 900px;
  background-image: url(../img/about-bg.png);
  background-size: cover;
  background-repeat: no-repeat; }

.about__title {
  text-align: center; }

.about__text {
  max-width: 720px;
  margin: 0 auto;
  margin-bottom: 50px; }

.about__video {
  width: 900px;
  margin: 0 auto;
  position: relative; }

.about__play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 64px;
  height: 64px;
  background-color: #ffffff;
  border: none;
  border-radius: 50%;
  box-shadow: 0px 0px 0px 16px rgba(255, 255, 255, 0.32); }

.about__play svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-33%, -50%); }

.programs {
  background-image: url(../../img/programs-bg.png);
  background-repeat: no-repeat;
  background-position: top right; }

.programs__inner {
  max-width: 852px;
  margin-left: 210px;
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap; }

.programs__title {
  flex-basis: 100%; }

.programs__list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 230px; }

.programs__item button {
  min-width: 230px;
  min-height: 70px;
  border: none;
  background-color: transparent;
  text-align: left; }

.programs__description {
  max-width: 600px; }

.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 25px 110px;
  margin-top: auto;
  border-top: 1px solid rgba(72, 72, 72, 0.2); }

.footer__copyright {
  font-size: 12px;
  line-height: 150%;
  color: #484848;
  opacity: 0.5; }

.footer__social {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.footer__social svg {
  opacity: 0.5; }

.footer__social li {
  margin-right: 25px; }
  .footer__social li:last-child {
    margin-right: 0; }

@media (max-width: 767px) {
  .footer {
    flex-direction: column;
    justify-content: center;
    padding: 32px 55px; }
  .footer__social {
    margin-top: 25px; } }

.call {
  display: none; }

.call form {
  width: 480px;
  height: 520px; }

.call__iagree-label {
  font-size: 13px;
  line-height: 150%;
  opacity: 0.74;
  display: block;
  position: relative;
  max-width: 240px;
  margin: 0 auto;
  padding-left: 24px; }
  .call__iagree-label::before, .call__iagree-label::after {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    position: absolute;
    top: 1px;
    left: 0;
    background: url(../img/icon-checkbox.svg) no-repeat; }
  .call__iagree-label::after {
    display: none;
    background: url(../img/icon-checkbox-checked.svg); }

.call__call__iagree-input:checked + .call__iagree-label::after {
  display: block; }

.accepted {
  display: none; }

.accepted__inner svg {
  display: block;
  margin: 0 auto; }

.accepted__inner h3 {
  text-align: center; }

.accepted__inner p {
  margin: 0 auto;
  text-align: center; }
